.horizontal-gallery-component {
    width: 100%;
    margin: auto;

    .five-container{
        width:100%;
        white-space: nowrap;
        overflow: auto;
        margin: auto;
        text-align: center;
        padding-bottom : 1rem;

        /* Works on Firefox */
        scrollbar-width: thin;
        scrollbar-color: rgba(0,0,0,0) var(--accent-color);

        &:hover {
            padding-bottom : 0.5rem;
        }

		/* Works on Chrome, Edge, and Safari */
		&::-webkit-scrollbar {
			height: 0.5rem;
			transition: height;
		}

		&:hover::-webkit-scrollbar {
			height: 1rem;
		}

        &::-webkit-scrollbar-track {
            background: rgba(0,0,0,0);
        }

        &::-webkit-scrollbar-thumb {
            background-color: var(--accent-color);
            border-radius: 1rem;
        }

        .image-container{
            width: 17.4rem;
            display: inline-block;
            margin: 0 0.3rem;
            background-size: cover;
            background-position: center;

            @media(max-width: 49rem){
                width: 12rem;
            }

            img{
                width: 100%;
            }
        }
    }
}