.component-inner-navigation {
	text-transform        : uppercase;
	z-index               : 10;
	max-width             : 100%;
	display               : grid;
	grid-template-columns : 10rem auto;
	padding               : 0 1rem;
	overflow              : hidden;

	-webkit-transition    : height 500ms,  background-color 0.5s; /* For Safari 3.1 to 6.0 */
	transition            : height 500ms,  background-color 0.5s;

	.logo-container {
		margin : auto;
		min-width: 10rem;
		@media (max-width : 38rem) {
			min-width: 8.7rem;
		}
	}

	a {
		color           : inherit;
		text-decoration : none;

		&:hover {
			color : var(--accent-color);
		}
	}

	> .links-container {
		max-width : 100%;

		@media (max-width : 38rem) {
			grid-column-start : 1;
			grid-column-end   : 3;
		}

		.links-list {
			padding    : 0;
			list-style : none;
			text-align : right;


			.link-container {
				display : inline-block;

				@media (max-width : 38rem) {
					display : block;
				}

				.navigation-link {
					margin         : 2rem 0.8rem 0;
					padding-bottom : 0.3rem;
					display        : inline-block;
					font-size      : 0.9rem;
					letter-spacing : 0.2rem;
					padding-left   : 0.3rem;

					@media (max-width : 60rem) {
						margin         : 2rem 0.5rem 0;
						font-size      : 0.9rem;
						letter-spacing : 0.05rem;
					}

					@media (max-width : 49rem) {
						margin         : 2rem 0.2rem 0;
						font-size      : 0.9rem;
						letter-spacing : 0;
					}

					@media (max-width : 38rem) {
						padding        : 0;
						margin         : 0.1rem 0;
						font-size      : 1.25rem;
						line-height    : 2.5rem;
						letter-spacing : 0.3rem;
					}

					&.active {
						border-bottom       : 0.3rem solid #aa8324;
						border-bottom-color : var(--accent-color, #aa8324);

						@media (max-width : 38rem) {
							border-bottom-width : 0.2rem;
						}
					}
				}
			}
		}
	}

	.open-close-button-container {
		display : none;

		@media (max-width : 38rem) {
			display : block;
		}

		.open-close-button {
			overflow  : hidden;
			opacity   : 0;
			margin    : 1rem;
			max-width : 2rem;
			padding   : 0.2rem;

			img {
				width : 100%;
			}

			.times {
				padding : 0.1rem;
			}

			@media (max-width : 38rem) {
				opacity   : 1;
				font-size : 2rem;
				float     : right;
				cursor    : pointer;
				margin    : 0.6rem 1rem;
			}

			&.dark img {
				filter: invert(1);
			}
		}
	}

	@media (max-width : 60rem) {
		letter-spacing : 0.05rem;
	}

	@media (max-width : 38rem) {
		font-size             : 1.5rem;
		font-weight           : 400;
		width                 : 100%;
		height                : 29.5rem;
		grid-template-rows    : 5rem auto;
		grid-template-columns : 5rem auto;
	}


	&.dark {

		a {
			color : white;
		}

		@media (max-width : 38rem) {
			background: rgba(0,0,0,0.9);
		}
	}

	&.closed {

		@media (max-width : 38rem) {
			height : 4rem;
			background: rgba(0,0,0,0);
		}
	}

	.book-now-container {
		font-size : 0.82rem;
		margin-right : 1.3rem;

		a {
			color: white;
		}

		@media (max-width : 38rem) {
			font-size : 1.36rem;
			margin-top : 1rem;
			margin-bottom : 1.4rem;
			margin-right : 0;
		}
	}

}
