.icons-top-component{
    width: 100%;
    margin-top: 7rem;
    text-align: center;

    @media (max-width: 49rem) {
        margin-top: 5rem;
    }

    @media (max-width: 38rem) {
        margin-top: 4rem;
        padding: 0 2rem;
    }

    .header{

        .title{
            font-size: 2.4rem;
            text-transform: uppercase;
            letter-spacing: 0.1rem;
            line-height: 2.5rem;

            @media (max-width: 49rem){
                font-size: 2rem;
            }
            @media (max-width: 30rem){
                font-size: 1.5rem;
                line-height: 1.8rem;
            }
        }
    }

    .icons-container{
        width: 100%;
        margin-top: 6rem;

        @media (max-width: 49rem) {
            margin-top: 3rem;
        }
        @media (max-width: 30rem) {
            margin-top: 1.5rem;
        }


        .content-container{
            display: inline-block;
            width: 33%;
            vertical-align: top;
            padding: 0 3rem;
            
            @media (max-width: 60rem) {
                padding: 0 1.5rem;
            }
            @media (max-width: 49rem) {
                padding: 0 1rem;
            }

            @media (max-width: 38rem) {
                padding: 0 1rem;
                width: 100%;
            }

            .icon{
                height: 3.1rem;
            }

            .text{
                margin-top: 1.5rem;
                font-size: 1.1rem;
                line-height: 1.9rem;
                font-weight: 400;

                @media (max-width: 49rem){
                    font-size: 1rem;
                    line-height: 1.5rem;
                }

            }

        }

    }
}