.six-icons-component {
    width: 100%;
    max-width: 60rem;
    margin: auto;


    .six-icon-containers {
        display: grid;
        grid-auto-flow: column;
        grid-template-columns: 40% 40%;
        grid-template-rows: repeat(3, 1fr);
        grid-gap: 3rem 10rem;
        justify-content: center;

        .title{
            text-transform: uppercase;
            font-size: 1.1rem;
            font-weight: 600;

            span{
                color: var(--accent-color);
            }

            @media(max-width: 38rem){
                font-size: 1rem;
            }
        }

        .icon{
            height: 2.8rem;
        }

        @media(max-width: 60rem){
            grid-gap: 3rem 5rem;
        }

        @media(max-width: 49rem){
            grid-gap: 2rem 5rem;
        }

        @media(max-width: 38rem){
            grid-gap: 2rem 3rem;
        }

        @media(max-width: 30rem){
            text-align: center;
            grid-template-columns: 80%;
            grid-auto-flow: row;
            grid-gap: 2rem;
        }
    }
}