.four-images-component {
    width: 100%;
    margin: auto;
    margin-top: 8rem;
    max-width: 70rem;

    @media (max-width: 70rem) {
        padding: 0 1rem;
    }

    @media (max-width: 49rem) {
        max-width: 40rem;
        text-align: center;
        padding: 0 0.5rem;
        margin-top: 5rem;

    }
    

    .text-container {
        display: inline-block;
        width: 55%;
        vertical-align: top;
        padding-right: 6rem;

        @media (max-width: 70rem) {
            padding-right: 3rem;
        }

        @media (max-width: 60rem) {
            padding-right: 0;
            width: 45%;
        }

        @media (max-width: 49rem) {
            width: 100%;
            text-align: left;
            padding-left: 1rem;
        }

        .title{
            font-size: 2.4rem;
            text-transform: uppercase;
            letter-spacing: 0.1rem;
            line-height: 2.5rem;
            margin-top: 0;

            @media (max-width: 60rem) {
                font-size: 2rem;
            }
        }

        .text{ 
            font-size: 1.1rem;
            line-height: 1.9rem;
            font-weight: 400;
        }

        .list{
            font-size: 1.1rem;
            line-height: 2rem;
            font-weight: 400;
            margin-top: 2rem;
            
            @media (max-width: 60rem) and (min-width: 49.1rem){
                line-height: 1.5rem;
            }

            @media (max-width: 49rem) {
                text-align: left;
            }
        }
    }

    .four-images-container {
        display: inline-block;
        width: 45%;
        max-width: 31.6rem;

        @media (max-width: 60rem) {
            width: 55%;
        }
        @media (max-width: 49rem) {
            width: 100%;
        }
        
        .four-images{
            display: inline-block;
            width: 44%;
            margin: 0 0 1.5rem 1.5rem;

            @media (max-width: 60rem) {
                margin: 0 0 1rem 1rem;
            }

            .image-container{
                background-repeat: no-repeat;
                background-size: cover;  
            }  
        }

        .left{
            @media (max-width: 49rem) {
                margin-left: 0;
            }
        }
    }
}