.mission-component{
    max-width: 1440px;
    margin-top: 5rem;
    width: 100%;
    padding-bottom: 12rem;

    @media (max-width: 60rem) {
        padding-bottom: 8rem;
    }

    @media (max-width: 49rem) {
        padding-bottom: 6rem;
    }

    @media (max-width: 38rem) {
        padding-bottom: 5rem;
    }

    @media (max-width: 25rem) {
        padding-bottom: 3rem;
    }

    .mission-text-container{
        display: inline-block;
        width: 60%;
        padding: 0 15%;
        line-height: 1.7rem;
        margin-top: 6%;

        @media (max-width: 60rem) {
            padding-left: 5%;
        }

        @media (max-width: 49rem) {
            padding-right: 8%;
            width: 50%;
        }
        
        @media (max-width: 38rem) {
            padding: 0 8%;
            padding-bottom: 3rem;
            width: 100%;
            margin-top: 0;
        }

        .title{
            font-size: 2.4rem;
            text-transform: uppercase;
            letter-spacing: 0.1rem;
            line-height: 2.5rem;

            @media (max-width: 49rem){
                font-size: 2rem;
            }
            @media (max-width: 30rem){
                font-size: 1.5rem;
                line-height: 2rem;
            }
        }
        .text{
            font-size: 1.1rem;
            line-height: 1.9rem;


            @media (max-width: 49rem){
                text-align: left;
                font-size: 1rem;
                line-height: 1.5rem;
            }
        }
    }

    .squares-container {
        display: inline-block;
        width: 40%;
        background-color: #e6dabe;
        vertical-align: top;

        @media (max-width: 49rem){
            width: 50%;
        }

        @media (max-width: 38rem){
            width: 85%;
            margin-left: 15%;
        }

        .text-container{
            background-color: #aa8324;
            margin: 15% 0;
            margin-left: -17%;
            width: 100%;
            padding: 8%;
            position: relative; 
            color: white;
            font-size: 0.95rem;
            line-height: 1.4rem;
            font-weight: 400;
            min-height: 23.34rem;
            

            @media (max-width: 49rem){
                margin-left: -12%;
            }

            @media (max-width: 38rem){
                margin-left: -12%;
            }

            @media (max-width: 30rem){
                padding: 6%;
                min-height: 21.75rem;
            }

            img{
                width: 100%;
                max-width: 5rem;
                
                @media (max-width: 75rem){
                max-width: 4rem;
                }

                @media (max-width: 30rem){
                max-width: 3rem;
                }
            }

            
            .inner-text-container{
                padding: 1rem 1.5rem;

                @media (max-width: 75rem){
                    padding: 0.5rem 1.5rem;
                }
                @media (max-width: 60rem){
                    padding: 1rem;
                }

                > p{
                margin: 0;
                }

                .quote-name-top{
                    margin-top: 1rem;
                }

                .quote-name-bottom{
                    font-style: italic;

                }
            }

            .change-button{
                position: absolute;
                top: 50%;
                cursor: pointer;
                font-size: 2rem;
                margin-top: -1rem; 
            }

            .next-button{
                right: 2rem;

                @media (max-width: 60rem){
                    right: 1rem;
                }
            }
            .previous-button {
                left: 2rem;

                @media (max-width: 60rem){
                    left: 1rem;
                }
            } 
        }
    }
}