.cesar-content-component {
    width: 100%;
    margin: auto;
    margin-top: 7rem;
    padding: 0 1.5rem;

    @media (max-width:38rem){
        margin-top: 1rem;
        padding: 0;

        .component-submit-button{
            text-align: center;
        }
    }

    .image-left-container{
        display: inline-block;
        width: 53%;
        vertical-align: middle;
        padding-right: 10%;

        @media (max-width: 49rem){
            padding-right: 5%;
        }
        
        .image-left{
            width: 100%;
            
            @media (max-width: 38rem){
            display: none;
            }
        }
    }

    .text-right-container { 
        text-align: left;
        display: inline-block;
        width: 47%;
        vertical-align: middle;


        @media (max-width: 38rem){
            width: 100%;
            padding: 0 2rem;
        }

        .image-half{
            display: none;

            @media (max-width: 38rem){
                display: initial;
                width: 100%;
                padding: 2rem 4rem;
            }

            @media (max-width: 30rem){
                padding: 2rem 2rem;
            }

            @media (max-width: 25rem){
                padding: 2rem 0rem;
            }
        }

        .title {
            font-size: 2.4rem;
            text-transform: uppercase;
            letter-spacing: 0.1rem;
            line-height: 2.5rem;
            margin: 1rem 0;

            @media (max-width: 60rem){
                font-size: 2.2rem;
            }
            @media (max-width: 49rem){
                font-size: 2rem;
            }

            @media (max-width: 38rem){
                margin-bottom: 2rem;
            }

            @media (max-width: 30rem){
                font-size: 1.5rem;
                line-height: 2rem;
            }
        }

        .text {
            font-size: 1.1rem;
            line-height: 1.9rem;
            width: 95%;


            @media (max-width: 49rem){
                line-height: 1.5rem;
                font-size: 1rem;
            }

            @media (max-width: 38rem){
                width: 100%;
        
            }
        }
    }
}