.component-six-lines{
    max-width: 100%;
    margin: auto;
    svg {
        display: block;
    }

    .vertical{
        -ms-transform: rotate(-90deg); /* IE 9 */
        -webkit-transform: rotate(-90deg); /* Safari 3-8 */
        transform: rotate(-90deg);
    }

    .white{
        fill: white;
        
        &.accent{
        fill : var(--accent-color);
        }
    }

    .black{
        fill: black;
        
        &.accent{
        fill : var(--accent-color);
        }
    }
 
}

