.component-mini-six-squares-logo {
	text-align: center;
	padding: 1rem;

	.mini-six-squares {
		width : 40%;
		display: grid;
		grid-template-columns: 30.66% 30.66% 30.66%;
		grid-gap: 4%;

		.component-six-lines {
			width: 100%;
		}

		margin : auto auto 6%;
	}

	.component-logo-sixth-sense-text {
		width: 100%;
		margin-bottom : 2%;
	}

	.component-logo-agency-text {
		width: 36%;
		margin: auto;
	}
}