.about-header-component{
    width: 100%;
    margin: auto;
    margin-top: 3rem;

    .content-container{
        width: 100%;
        margin: auto; 
        background-repeat: no-repeat;
        background-position: top right;/* 
        background-size: contain; */
        background-size: 65%;
        padding-bottom: 10rem;

        @media (max-width:75rem){
            background-size: 70%;
        }
        @media (max-width:60rem){
            padding-bottom: 6rem;
            background-size: 80%;
        }

        @media (max-width:49rem){
            padding-bottom: 4rem;
            background-size: 100%;
        }

        @media (max-width:38rem){
            background-position: top right;
            background-size: 100% 70%;
        }

        @media (max-width:30rem){
            background-size: 100% 62%;  
        }

        @media (max-width:25rem){
            background-size: 100% 50%;   
        }

        .title{
            max-width: 60rem;
            margin: auto;
            padding-right: 16.5rem;
            padding-top: 1rem;

            @media (max-width:60rem) {
                padding-left: 2rem;
                padding-right: 10rem;

            }

            @media (max-width:49rem) {
                padding-right: 2rem;
            }

            @media (max-width:38rem) {
                padding-right: 0;
                padding-top: 0.1rem;
            }
        }

        > .text{
            max-width: 60rem;
            margin: auto;
            padding-right: 10rem;
            font-size: 1.1rem;
            line-height: 1.7rem;
            font-weight: 400;

            @media (max-width:60rem) {
                padding-left: 2rem;
                padding-right: 10rem;
            }

            @media (max-width:49rem) {
                padding-right: 2rem;
                padding-top: 2rem;
            }


        }
    }
}