.title-component {

  .text {
    font-size: 3.75rem;
    line-height: 4.3rem;
    padding-right: 0.5rem;
    margin-bottom: 1.44rem;
    font-weight: 400;
    letter-spacing: 0.1rem;

    @media (max-width: 38rem) {
      font-size: 3rem;
      line-height: 3.5rem;
    }

    @media (max-width: 30rem) {
      font-size: 2.5rem;
      line-height: 3rem;
    }

    @media (max-width: 25rem) {
      font-size: 2rem;
      line-height: 2.4rem;
    }
  }
}