.component-submit-button {
  margin-top: 2rem;
  button {
    cursor: pointer;
    text-transform: uppercase;
    min-width: 13rem;
    border-radius: 50rem;
    border: none;
    padding: 1rem;
    background: #1e1e1e;
    background: var(--dark-background-color, #1e1e1e);
    color: #fff;
    font-weight: 600;
    letter-spacing: 0.0875rem;
    font-size: 0.875rem;
    &:active {
      background: black;
    }
    &[disabled] {
      background: lightgray;
    }
    &.dark-bg {
      background: white;
      color: black;
      &:active {
        background: beige;
      }
      &[disabled] {
        background: #e4e4e4;
        color: #ababab;
      }
    }

    @media(max-width: 49rem){
      min-width: 11rem;
      padding: 0.9rem;
      font-size: 0.8rem;
      letter-spacing: 0.15rem;
      font-weight: 500;
    }
  }
}