.staff-tabs-component{
    width: 100%;
    text-align: center;
    margin-top: 3rem;
    padding: 0 1rem;

    @media (max-width: 60rem) {
        margin-top: 1.5rem;
    }

    @media (max-width: 38rem) {
        margin-top: 0;
    }

    .staff-tabs-title{
        font-size: 2.4rem;
        text-transform: uppercase;
        letter-spacing: 0.1rem;
        line-height: 3rem;
        margin-top: 0;
        margin-bottom: 8.5rem;
        font-weight: 400;

        @media (max-width: 60rem) {
               font-size: 2rem;
               line-height: 2.5rem;
               margin-bottom: 5rem;
        }

        @media (max-width: 38rem) {
               font-size: 1.5rem;
               line-height: 2rem;
               margin-bottom: 2.5rem;
        }
    }

    .icons-tabs {
        cursor: pointer;
        width:100%;

        .tab-container{
            display: inline-block;
            width: 25%;
            vertical-align: top;

            @media (max-width: 38rem) {
                width: 50%;
                margin-top: 1rem;
            }

            .top-icon{
                height: 4.8rem;

                @media (max-width: 49rem){
                    height: 3.8rem;
                }
            }

            .icon-title {
                text-transform: uppercase;
                font-size: 1rem;
                font-weight: 500;

                @media (max-width: 49rem) {
                    margin: 1rem 0.5rem;
                    font-size: 0.9rem;
                }
            }

            .golden-line {
                background-color: var(--accent-color);
                height: 0.3rem;

                &.hidden{
                    display: none;
                }
            }
        }

    }

    .dinamic-content-container{
        margin: 0;
        box-shadow: 0 0.3rem 1.37rem 0 rgba(0, 0, 0, 0.18);
        padding: 6rem 5rem;
        text-align: left;

        @media (max-width: 49rem) {
            padding: 2rem;
        }

        @media (max-width: 30rem) {
            padding: 0.5rem;
        }

        .icon-container{
            display: inline-block;
            vertical-align: middle;
            width: 30%;
            position: relative;
            height: 11rem;
            min-width: 10rem;
            text-align: center;
            background-repeat: no-repeat;
            background-position: center;
            background-size: contain;
            margin-left: -2rem;

            @media (max-width: 49rem) {
                margin-left: -1rem;
            }

            @media (max-width: 38rem) {
                width: 100%;
                margin-right: 0;
                margin: 1.5rem 0;
            }
            

            .dinamic-icon {
                margin-top: 3.5rem;
                width: 4.1rem;

                @media (max-width: 49rem) {
                    width: 3.8rem;
                }
            }
            
        }

        .text-container{
            display: inline-block;
            vertical-align: middle;
            padding-left: 5%;
            width: 70%;
            text-align: left;

            @media (max-width: 38rem) {
                width: 100%;
                padding: 0 5%;
            }

            .content-title {
                text-transform: uppercase;
                font-size: 1.1rem;
                font-weight: 600;

                @media (max-width: 49rem) {
                    font-size: 1rem;
                    font-weight: 600;
                }
            }

            .content-text {
                font-size: 1.1rem;
                line-height: 1.75rem;
                font-weight: 400;
                letter-spacing: 0.05rem;

                @media (max-width: 49rem) {
                    font-size: 1rem;
                }
            }

        }

    }

    

    
}