.six-grid-images-component {
    width: 100%;
    margin: auto;
    margin-top: 10rem;

    @media (max-width: 49rem) {
        margin-top: 5rem;
    }

    @media (max-width: 38rem) {
        margin-top: 1rem;
    }
    @media (max-width: 30rem) {
        padding-right: 3rem;
        padding-left: 3rem;
    }

    @media (max-width: 30rem) {
        padding-right: 1rem;
        padding-left: 1rem;
    }

    .six-containers{
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-gap: 2rem;

        .image-container{
            position: relative;
            display: grid;
            background-size: cover;
            background-position: center;
             
            .text-container{
                position: absolute;
                top:50%;
                left: 0;
                right: 0;
                bottom: 0;
                margin-top: -3.3rem;

                @media(max-width: 49rem){
                    margin-top: -2.7rem;
                }

                .text{
                    text-transform: uppercase;
                    color: white;
                    font-size: 1.75rem;
                    line-height: 2.2rem;
                    letter-spacing: 0.2rem;
                    font-weight: 500;
                    margin: auto;
                    text-align: center;

                    /* @media(max-width: 49rem) and (min-width: 38.1rem){
                        font-size: 1.5rem;
                        line-height: 2rem;
                    } */
                }

                &.short{
                    margin-top: -2.2rem;

                    @media(max-width: 49rem){
                        margin-top: -1.7rem;
                    }
                }
            }
            
        }
        @media(max-width: 60rem){
            grid-gap: 1rem;
        }
        @media(max-width: 49rem){
            grid-template-columns: 1fr 1fr;
        }
        @media(max-width: 38rem){
            grid-gap: 0.3rem;
        }
        @media(max-width: 30rem){
            grid-template-columns: 1fr;
        }
    }

}