.component-input {
  label{
    margin-top: 0.5rem;
    -webkit-transition: opacity 500ms, transform 500ms; /* For Safari 3.1 to 6.0 */
    transition: opacity 500ms, transform 500ms;
    transform: translateY(1.5rem);
    opacity: 0;
    display: block;
    font-size: 0.8rem;
    pointer-events: none;
  }

  input, textarea, select {
    background: none;
    font-size: 1rem;
    font-family: inherit;
    border: none;
    border-bottom: 1px black solid;
    display: block;
    width: 100%;
    max-width: 100%;
    min-width: 100%;
    min-height: 1rem;
    padding: 0.5rem 0 0.5rem 0.2rem;
    &::placeholder {
      color: black;
    }
    &.dark-bg {
      color: white;
      border-bottom-color: white;
      &::placeholder {
        color: white;
      }
    }
    &.invalid {
      border-bottom-color: red;
    }
    &[type="date"] {
      padding: 0.35rem 0;
    }
    &:active, &:focus {
      outline: 1px solid #aa8324;
      outline: 1px solid var(--accent-color, #aa8324);
    }
  }
  &.show-label {
    label{
      transform: translateY(0);
      opacity: 1;
      display: block;
    }
  }
}