.infopack-component{
    width: 100%;
    margin-top: 8rem;
    background-color: #2f2f2f;
    text-align: center;
    padding: 3.5rem 0;
    padding-bottom: 4rem;


    @media (max-width: 49rem){
        padding: 2.5rem 1rem;
        padding-bottom: 4rem;
        margin-top: 5rem;
    }

    .text {
        color: white;
        font-size: 2.3rem;
        margin: 1rem auto;
        line-height: 3rem;
        letter-spacing: 0.1rem;
        padding: 0 11rem;
        //text-transform: capitalize;
        margin-bottom: 3rem;

        @media (max-width: 60rem){
            padding: 0 6rem;
            font-size: 2rem;
        }

        @media (max-width: 49rem){
            padding: 0 2.3rem;
        }

        @media (max-width: 38rem){
            font-size: 1.5rem;
            line-height: 2.3rem;
            padding: 0 1.5rem;

        }
    }
}