.events-header-component {
    width: 100%;
    margin: auto;
    margin-top: 3rem;

    .title-component .text {
        @media (max-width:25rem) {
            margin-bottom: 0.5rem;
        }
    }
    
    .top-container{
        width: 100%;
        max-width: 77.5rem;
        margin: auto; 
        background-repeat: no-repeat;
        background-size: contain;

        @media (max-width:49rem){
            background-position: top left;
            background-position-y: -3rem;
        }

        @media (max-width:70rem) and (min-width:49.1rem) {
            padding-bottom: 10rem;
        }

        @media (max-width:30rem){
            background-position-y: 0;
        }

        .title{
            display: inline-block;
            width: 50%;
            vertical-align: top;
            padding-left: 9rem; 
            padding-top: 0.5rem; 

            @media (max-width:49rem){
                width: 80%;
                padding-left: 7rem;
                
            }

            @media (max-width:38rem){
                padding-left: 6rem;
                padding-top: 0;
                width: 95%;
            }
            @media (max-width:30rem){
                padding-left: 5rem;
                margin-top: 0.2rem;
            }

            @media (max-width:25rem){
                padding-left: 4rem;
            }

            .subtitle{
                font-weight: 600;
                margin-top: -0.5rem;

                @media (max-width:30rem){
                    font-weight: 500;
                    margin-top: 0;
                }
            } 
        }

        .image-container{
            display: inline-block;
            width: 50%;
            vertical-align: bottom;
            margin-top: 8rem;
            text-align: right;

            @media (max-width:70rem) and (min-width: 49.1rem){
                display: none;
            }

            @media (max-width:49rem){
                width: 100%;
                text-align: center;
                padding: 0 1rem;
            }

            @media (max-width:38rem){
                margin-top: 3rem;
            }


            img{
                width: 100%;
                max-width: 34.68rem;

                @media (max-width:75rem) {
                    max-width: 33rem;
                }
                
            }

        }
    }

    .text-container {
        width: 100%;
        max-width: 53rem;
        margin: auto;
        font-size: 1.25rem;
        line-height: 2rem;
        font-weight: 400;
        margin-top: 4rem;

        @media (max-width:70rem){
            max-width: none;
            margin-top: 0;
        }


        .image-container-bottom{
            display: none;

            @media (max-width:70rem) and (min-width:49.1rem){
                display: inline-block;
                width: 50%;
                margin-top: -8rem;
                justify-content: right;
            }

            @media (max-width:60rem) {
                width: 55%;
            }

            img{
                max-width: 100%;
                padding-right: 3rem;

                @media (max-width:60rem) {
                    padding-right: 1.5rem;
                }
            }
        }

        .text{
            margin:0;

            @media (max-width:70rem){
                display: inline-block;
                width: 50%;
                padding: 0 3rem;
            }

            @media (max-width:60rem) {
                padding: 0 1.5rem;
                width: 45%;
                font-size: 1.1rem;
            }

            @media (max-width:49rem) {
                padding: 0 2.5rem;
                width: 100%;
                font-size: 1.25rem;
                margin-top: 2rem;
            }

            @media (max-width:38rem){
                margin-top: 1rem;
                padding: 0 1.5rem;
                font-size: 1.1rem;

            }
        }
        .text2{
            width: 100%;
            font-size: 1.25rem;
            margin:0;

            @media (max-width:70rem){
                padding: 0 3rem;
            }

            @media (max-width:60rem) and (min-width: 49.1rem){
                padding: 0 1.5rem;
                font-size: 1.1rem;
            }
            @media (max-width:49rem){
                padding: 0 2.5rem;

            }
            @media (max-width:38rem){
                padding: 0 1.5rem;
                font-size: 1.1rem;

            }
        }
    }
}