.component-jobs-form {
	max-width: 32rem;
	padding: 0 1rem;
	margin: auto;
	.title {
		text-align: center;
		h2 {
			margin-top : 10rem;
			font-size: 2.375rem;
			line-height: 3.125rem;
			margin-bottom: 0.2rem;
			text-transform: uppercase;
		}
		.gold-bar {
			display: inline-block;
			width: 7.5rem;
			height: 0.3rem;
			background-color: #aa8324;
			background-color: var(--accent-color, #aa8324);
		}
	}

	.small-fields{
		margin: 0 -0.5rem 2rem;
		.small-field {
			display: inline-block;
			width: 50%;
			padding: 0 0.5rem;
			@media (max-width: 30rem) {
				width: 100%;
			}
		}

	}

	.component-input {
		input, textarea, select {
			font-size: 1.25rem;
		}
	}
	.component-submit-button {
		text-align: center;
	}
}