.component-text-left-image {
    max-width: 72rem;
    margin: auto;
    margin-top: 5rem;
    background-repeat: no-repeat;
    background-position: bottom right;
    padding-bottom: 6rem;

    @media (max-width: 75rem){
        padding-right: 5rem;
    }

    @media (max-width: 70rem){
        padding-bottom: 2rem;
    }

    @media (max-width: 60rem){
        margin-top: 3rem;
        background: none !important;
        padding: 0 1rem;
    }

    @media (max-width: 38rem){
        padding: 0 2rem;
    }
    @media (max-width: 30rem){
        padding: 0 1rem;
    }
    @media (max-width: 25rem){
        padding: 0 0.5rem;
    }
    
    .medium-size-component{
        max-width: 60rem;
        margin: auto;

        .jobs-title{
            margin: auto;
            padding: 2rem 23rem 3rem 4rem;
            background-repeat: no-repeat;
            background-position: top left;
            margin-left: -4rem;

            @media (max-width:70rem) {
                margin-left: 0;
                padding-right: 15rem;
            }
            @media (max-width:60rem) {
                padding: 1rem 6rem 1rem 3rem;
            }

            @media (max-width:49rem) {
                padding: 0.5rem 2rem;
            }

             @media (max-width: 38rem){
                padding: 0.5rem 1rem;
            }
        }

        .text-left-container { 
            text-align: left;
            display: inline-block;
            vertical-align: top;
            width: 37%;
            margin-right: 8.5%;
            margin-top: 3rem;

            @media (max-width:70rem) {
                margin-left: 2rem;
                margin-right: 2rem;
            }
            @media (max-width:60rem) {
                margin-top: 1rem;
            }

            @media (max-width: 49rem){
                margin: 0;
                width: 50%;
                padding: 1rem;
            }
            @media (max-width: 38rem){
                width: 100%;
                margin-right: 0;
                padding: 1rem;
            }
            @media (max-width: 30rem){
                padding: 1rem 0.5rem;
            }

            .text{
                padding-bottom: 1rem;
                font-size: 1.1rem;
                line-height: 1.75rem;
                font-weight: 400;
                margin: 0;
                
                @media (max-width: 49rem){
                    font-size: 1rem;
                    line-height: 1.4rem;
                }

                @media (max-width: 38rem){
                    padding-bottom: 1rem;
                }
                 @media (max-width: 30rem){
                    padding: 0;
                } 
            }
        }

        .image-right-container{
            display: inline-block;
            width: 54.3%;
            vertical-align: top;
            background-size: cover;
            justify-content: right;
            margin-top: -5rem;

            @media (max-width:60rem) {
                margin-top: -2rem;
            }
            @media (max-width:49rem) {
                width: 50%;
                margin-top: 1rem;
            }
            
            @media (max-width: 38rem){
                width: 100%;
                justify-content: center;
                margin-top: 0rem;
            }

            .image-right{
                width: 100%;
            }
        }
    }
}