.component-footer {
	text-align: center;
	font-size: 1rem;
	font-weight: 300;
	margin-top: 3rem;

	a {
		color: inherit;
		text-decoration: none;
		letter-spacing: 0.3rem;
		-webkit-transition: color 500ms; /* For Safari 3.1 to 6.0 */
		transition: color 500ms;
		text-transform: uppercase;

		&:hover, &.active {
			color: var(--accent-color);
		}
	}

	> .links-container {
		max-width: 100%;
		margin: auto;

		.logo-container {
			text-align: center;

			.component-logo {
				display: inline-block;
			}
		}

		.links-list {
			padding: 0;
			list-style: none;
		}

		.link-container {
			margin: 0.5rem 1.5rem;
			display: inline-block;

			@media (max-width: 49rem) {
				margin: 0.5rem 0.8rem;
			}
		}
	}

	.copyrigth-container {
		margin-top: 4rem;
		font-size: 1rem;
		color: gray;
		font-weight: 100;
		letter-spacing: -0.05rem
	}
}