.staff-header-component{
    width: 100%;
    margin: auto;
    margin-top: 3rem;

     .content-container{
        width: 100%;
        margin: auto; 
        background-repeat: no-repeat;
        background-position: top right;
        background-size: contain;
        padding-bottom: 12rem;

        @media (max-width:49rem){
            padding-bottom: 4rem;
        }

        .title{
            max-width: 60rem;
            margin: auto;
            padding-top: 5rem;

            @media (max-width:60rem) {
                padding-left: 2rem;
            }

            @media (max-width:49rem) {
                padding-top: 2rem;

            }

            @media (max-width:38rem) {
                padding-left: 1rem;
                padding-top: 1rem;

            }
        }

        > .text{
            max-width: 60rem;
            margin: auto;
            padding-right: 16rem;
            font-size: 1.1rem;
            line-height: 1.7rem;
            font-weight: 400;
            margin-top: 1rem;

            @media (max-width:60rem) {
                padding-left: 2rem;
                padding-right: 10rem;
            }

            @media (max-width:49rem) {
                padding-right: 2rem;
            }

            @media (max-width:38rem) {
                padding-right: 1rem;
                padding-left: 1rem;
                margin-top: 4.8rem;
            }
        }
    }
}