.start-chat-component{
    width: 100%;
    text-align: center;
    max-width: 48rem;
    margin: auto;
    margin-top: 5rem;
    padding: 0 1rem;

    .title{
        text-transform: uppercase;
        font-size: 2.4rem;
        line-height: 3.4rem;
        margin-bottom: 4rem;

        @media (max-width: 38rem) {
            font-size: 2rem;
            line-height: 3rem;
            margin-bottom: 2rem;
        }

        @media (max-width: 30rem) {
            font-size: 1.5rem;
            line-height: 2rem;
            margin-bottom: 2rem;
        }
    }

    .text{
        
        font-size: 1rem;
        line-height: 1.7rem;
        font-weight: 400;
    }
}