.three-steps-component {
    width: 100%;
    margin: auto;
    margin-top: 7rem;
    text-align: center;

    @media (max-width:75rem){
        padding: 0 3rem;
    }

    @media (max-width:60rem) {
        padding: 0 1.5rem;
    }
    @media (max-width:38rem) {
        margin-top: 5rem;
    }


    .number-container{
        display: inline-block;
        width: 33%;
        background-repeat: no-repeat;
        height: 16.3rem;
        background-size: contain; 
        vertical-align: top;
        padding-top: 4rem;

        
        @media (max-width:60rem) {
            padding-top: 2rem;
        }

        @media (max-width:49rem) and (min-width:38.1rem) {
            padding-top: 0;
        }

        @media (max-width:38rem) {
            width: 100%;
            max-width: 25rem;
            margin-bottom: 3rem;   
        }

        .icon-container {
            display: inline-block;
            width: 16%;
            vertical-align: top;

            @media (max-width:49rem) {
                width: 22%;
            }

            .icon{
                width: 2.5rem;
            }

        }

        .text-container {
            display: inline-block;
            width: 84%;
            text-align: left;

            @media (max-width:49rem) {
                width: 78%;
            }


            .title {
                font-size: 1.5rem;
                text-transform: uppercase;
                font-weight: 600;
                margin-top: 0;

                @media (max-width:49rem) and (min-width:38.1rem) {
                    margin-bottom: 0.5rem;
                    font-size: 1.3rem;
                }
            }

            .text {
                font-size: 1rem;
                line-height: 1.37rem;
                font-weight: 400;

                @media (max-width:49rem) and (min-width:38.1rem) {
                    font-size: 0.9rem;
                }

            }

        }

    }

}
