.component-gallery-11-elements {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-areas:
          "im1 im1 im1 im1 im2 im2"
          "im3 im3 im5 im5 im5 im5"
          "im4 im4 im5 im5 im5 im5"
          "im6 im6 im6 im7 im7 im7"
          "im8 im8 im9 im9 im10 im10"
          "im11 im11 im12 im12 im12 im12"
  ;
  grid-gap: 1rem;

  @media (max-width: 38rem) {
    grid-gap: 0.5rem;
  }

  .bg-image-container {
    cursor: pointer;
    @for $i from 1 through 12 {
      &:nth-child(#{$i}) {
        grid-area: im#{$i};
      }
    }
  }
}