.open-positions-component {
    padding: 0 0.5rem;
    font-size: 1.1rem;
    background-repeat: no-repeat;
    padding: 4rem 1rem 0;

    @media (max-width: 38rem) {
        background-image: none !important;
    }

    @media (max-width: 60rem) {
        padding: 4rem 1rem 0;
    }
    @media (max-width: 38rem) {
        padding: 2rem 1rem 0;
    }
    @media (max-width: 25rem) {
        padding: 2rem 2rem 0;
    }

    .header{
        line-height: 1.9rem;
        font-weight: 400;
        max-width: 58rem;
        margin: auto;
        font-size: 1.1rem;

            @media (max-width: 75rem){
                padding-left: 3rem;
            }

            @media (max-width: 49rem){
                font-size: 1rem;
                line-height: 1.5rem;
                padding-left: 1rem;
            }
            @media (max-width: 30rem){
                padding-left: 0;
            }
    }

    h2 {
        font-size: 2.4rem;
            text-transform: uppercase;
            letter-spacing: 0.1rem;
            line-height: 2.5rem;

            @media (max-width: 49rem){
                font-size: 2rem;
            }
            @media (max-width: 30rem){
                font-size: 1.5rem;
                line-height: 2rem;
            }
    }

    .positions-container {
        display: grid;
        grid-gap: 0.5rem;

        @media (min-width: 30rem) {
            grid-template-columns: 1fr 1fr;
            grid-gap: 1rem;
        }

        @media (min-width: 38rem) {
            margin-top: 3rem;
        }

        @media (min-width: 49rem) {
            grid-template-columns: 1fr 1fr 1fr;
            grid-gap: 1.2rem;
            max-width: 51.5rem;
            margin: 7.3rem auto 0;
        }

        .position-element {
            outline: none;
            text-transform: uppercase;
            background-color: #ece4cd;
            padding: 3rem 1rem;
            text-align: center;
            font-weight: 500;
            cursor: pointer;

            -webkit-transition: background-color 200ms; /* For Safari 3.1 to 6.0 */
            transition: background-color 200ms;
            &:active, &:hover {
                background-color: #d0b76d;
            }
        }
    }
}