.content-container-component {
	max-width: 70rem;
	margin: auto;
}
.content-container-full {
	max-width: 90rem;
	margin: auto;
}
.content-container-small {
	max-width: 60rem;
	margin: auto;
}

.about-page-component{
	.infopack-component{
		.text{
			padding: 0 4rem;

			@media (max-width: 49rem){
				padding: 0 2.3rem;
			}
			@media (max-width: 38rem){
				padding: 0 1.5rem;
			}
		}
	}
}
