.component-input {
	&.file-input {
		font-size: 1.25rem;
		font-weight: 400;

		label {
			font-size: 1.25rem;
			-webkit-transition: background-color 500ms; /* For Safari 3.1 to 6.0 */
			transition: background-color 500ms;
			opacity: 1;
			border-bottom: 1px solid black;
			padding: 1rem 0;
			cursor: pointer;
			overflow: hidden;
			text-overflow: ellipsis;
			margin-top: 0.5rem;
			transform: none;
			pointer-events: auto;

			.delete-file-button {
				float: right;
				z-index: 1;
				margin-left: 0.5rem;
				font-size: 2.5rem;
				line-height: 1rem;
			}

			.upload-file-button {
				float: right;
				z-index: 1;
				margin-left: 0.5rem;
				height: 1.2rem;
			}


			&.chosen {
				background-color: #F9F9F9;
			}

			&:hover {
				background-color: #e6e6e6;
			}

			&:active {
				background-color: #c3c3c3;
			}

			&.invalid {
				border-color: red;
			}
		}

		input {
			display: none;
		}

		.file-input-note {
			font-size: 0.75rem;
			margin-top: 1rem;
			margin-bottom: 0.7rem;
		}
	}
}