.component-alert {
    margin-top: 1rem;
    padding : 0.5rem 1rem;
    border : 1px solid;
    border-radius: 0.2rem;
    &.default {
        background: #c8eaff;
        border-color : #6bceff;
        color: #004567;
    }
    &.success {
        background: #b5eab8;
        border-color : #00bf0c;
        color: #006509;
    }
    &.warning {
        background: #ffe7a8;
        border-color : #ffc053;
        color: #5f3d00;
    }
    &.danger {
        background: #ffc6d0;
        border-color : #ff6471;
        color: red;
    }
}